
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.form {
  background-color: #15172b;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 20px;
  width: 500px;
}

.form2 {
  background-color: #15172b;
  border-radius: 20px;
  box-sizing: border-box;
  padding: 20px;
  width: 530px;
}

.title {
  color: rgb(180, 150, 150);
  font-family: Montserrat-Regular;
  font-size: 25px;
  font-weight: 600;
  margin-top: 15px;
}

.title2 {
  color: rgb(180, 150, 150);
  font-family: Montserrat-Regular;
  font-size: 18px;
}


.disclaimer {
  color: rgb(211, 193, 104);
  font-family: Montserrat-Regular;
  font-size: 15px;
}

.subtitle {
  color: #eee;
  font-family: Montserrat-Regular;
  font-size: 16px;
  font-weight: 600;
  margin-top: 10px;
}

.plaintext {
  color: rgba(107, 48, 48, 0.596);
  font-family: Montserrat-Regular;
  font-size: 13px;
  font-weight: 600;
  margin-top: 5px;
  margin-bottom: 5px;
}

.warning {
  color: rgba(18, 48, 145, 0.534);
  font-family: Montserrat-Regular;
  font-size: 12px;
  font-weight: 600;
  margin-top: 5px;
}

.input-container {
  height: 50px;
  position: relative;
  width: 100%;
}


.ic1 {
  margin-top: 40px;
}

.ic2 {
  margin-top: 30px;
}

.input {
  background-color: #303245;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  font-size: 18px;
  height: 100%;
  outline: 0;
  padding: 4px 20px 0;
  width: 100%;
}

.cut {
  background-color: #15172b;
  border-radius: 10px;
  height: 20px;
  left: 20px;
  position: absolute;
  top: -20px;
  transform: translateY(0);
  transition: transform 200ms;
  width: 76px;
}

.cut-short {
  width: 50px;
}

.input:focus ~ .cut,
.input:not(:placeholder-shown) ~ .cut {
  transform: translateY(8px);
}

.placeholder {
  color: #65657b;
  font-family: Montserrat-Regular;
  left: 20px;
  line-height: 14px;
  pointer-events: none;
  position: absolute;
  transform-origin: 0 50%;
  transition: transform 200ms, color 200ms;
  top: 20px;
}

.radioLabel {
  color: #65657b;
  font-family: Montserrat-Regular;
  left: 20px;
}

.input:focus ~ .placeholder,
.input:not(:placeholder-shown) ~ .placeholder {
  transform: translateY(-30px) translateX(10px) scale(0.75);
}

.input:not(:placeholder-shown) ~ .placeholder {
  color: #808097;
}

.input:focus ~ .placeholder {
  color: #dc2f55;
}

.submit {
  background-color: #08d;
  border-radius: 12px;
  border: 0;
  box-sizing: border-box;
  color: #eee;
  cursor: pointer;
  font-size: 18px;
  height: 50px;
  margin-top: 38px;
  outline: 0;
  text-align: center;
  width: 100%;
}

.submit:active {
  background-color: #06b;
}


.container {
  display:block;
  
}

body{
  background-color: rgb(192, 214, 231);
}


